    methods: {
      ...mapMutations(['changeMemo']),
      addTodo(e) {
      },
      eraseMemo() {
        this.textValue = ''
      },
      handleConfirm() {
        this.saveMemo()
      },
      back() {
        uni.showModal({
          title: '是否保存修改',
          confirmText: '保存修改',
          cancelText: '取消修改',
          success: (res) => {
            if (res.confirm) {
              // 写保存修改的代码
              this.saveMemo()
              return
            }
            if (res.cancel) {
              // 不保存的代码
              uni.navigateBack()
              return
            }
          }
        })
      },
      saveMemo() {
        if (this.textValue === '') {
          uni.showToast({
            title: '内容不可以为空',
            icon: 'none'
          })
          return
        }
        this.changeMemo(this.textValue)
        if (this.type === 'edit') {
          uni.$emit('changeMemo')
        } else if (this.type === 'add') {
          uni.$emit('addMemo')
        }
        uni.navigateBack()
      }
    }
	}
